<template>
  <div class="row my-5 border-top border-3 pt-5">
    <div class="category-carousel">
      <div class="card-container" ref="cardContainer">
        <div
          v-for="category in categories"
          :key="category.id"
          class="card"
          @click="handleCardClick(category)"
        >
          <img
            :src="require(`@/assets/svgs/${category.svgName}`)"
            alt="Category Icon"
            class="card-image"
          />

          <div class="card-text fw-bold">{{ category.categoryText }}</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import categoriesData from "@/data/categories.json";
import axios from "@/services/axios.js";

export default {
  name: "CategoriesComponent",
  data() {
    return {
      categories: categoriesData,
    };
  },
  methods: {
    handleCardClick(category) {
      console.log(`Clicked on ${category.categoryText}`);
    },
    fetchData() {
      axios
        .get("/data")
        .then((response) => {
          console.log(response.data);
        })
        .catch((error) => {
          console.error(error);
        });
    },
  },
  mounted() {
    // Set up horizontal scroll for the card container
    const cardContainer = this.$refs.cardContainer;
    cardContainer.addEventListener("wheel", (event) => {
      cardContainer.scrollLeft += event.deltaY;
      event.preventDefault();
    });
  },
};
</script>
<style scoped>
.category-carousel {
  overflow: hidden;
}

.card-container {
  display: flex;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  -webkit-overflow-scrolling: touch;
}

.card {
  flex: 0 0 auto;
  margin-right: 16px;
  padding: 16px;
  background-color: #f8f8f8;
  border-radius: 8px;
  cursor: pointer;
  scroll-snap-align: start;
}

.card-image {
  width: 64px;
  height: 64px;
  margin-bottom: 8px;
}

.card-text {
  text-align: center;
}
</style>
